import { UsuarioProvider } from './../usuario/usuario';

import { Injectable } from '@angular/core';
import { Geolocation } from '@ionic-native/geolocation';
import { AngularFirestore, AngularFirestoreDocument } from 'angularfire2/firestore';
import { Subscription } from 'rxjs/subscription';

@Injectable()
export class UbicacionProvider {

  chofer: AngularFirestoreDocument<any>;
  private watch: Subscription;

  constructor( private _geolocation: Geolocation,
               public _usuarioProvider: UsuarioProvider,
               private afDB: AngularFirestore ) { 
        
  }

  inicializarChofer() {
    if (!this.chofer)
    {
      this.chofer = this.afDB.doc(`/usuarios/${this._usuarioProvider.clave}`);
    }
  }

  iniciarGeolocalizacion() {
   
    console.log('ubicacion.ts - iniciarGeolocalizacion de ' , this._usuarioProvider.clave);
    this._geolocation.getCurrentPosition().then((resp) => {
      this.chofer.update({
          lat: resp.coords.latitude,
          lon: resp.coords.longitude,
          clave: this._usuarioProvider.clave
      });

      /* Una vez obtenida la posicion, estar al pendiente de los cambios */
      this.watch = this._geolocation.watchPosition().subscribe((data) => {
                  this.chofer.update({
                      lat: data.coords.latitude,
                      lon: data.coords.longitude,
                      clave: this._usuarioProvider.clave                      
                  });
                  // console.log('ubicacion.ts - iniciarGeolocalizacion (en watch) ' ,data.coords);
      });


     }).catch((error) => {
       console.log('Error al obtener locación', error);
     });
  }

  detenerUbicacion() {
    console.log('ubicacion.ts - detenerUbicacion');
    try{
      this.chofer = null;
      this.watch.unsubscribe();
    } catch (e) {
        console.log(JSON.stringify(e));
    }
  }

}



