import { Platform } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { Injectable } from '@angular/core';
import { AngularFirestore } from 'angularfire2/firestore';
import { Subscription } from 'rxjs/subscription';

@Injectable()
export class UsuarioProvider {

  private doc: Subscription;
  clave: string;
  // user: any= {};

  constructor( private afDB: AngularFirestore, 
               private storage: Storage,
               private platform: Platform ) {
  }

  verificaUsuario(clave: string) {
    console.log('usuario.ts función verificaUsuario ', clave);
    clave = clave.toLowerCase();
    return new Promise( (resolve, reject) => {
          
          this.doc = this.afDB.doc(`/usuarios/${clave}`)
                              .valueChanges()
                              .subscribe( data => {
                                    if (data){
                                      this.clave = clave;
                                     // this.user = data;
                                     
                                      this.guardarStorage();
                                      resolve(true);
                                    }
                                    else {  
                                       // incorrecto
                                       resolve(false);
                                    } 
                              });
        });
  }

  guardarStorage() {
    console.log('usuario.ts función guardarStorage ', this.clave);
    
    if (this.platform.is('cordova'))  {
       // dispositivo móvil
       this.storage.set('clave', this.clave);
    } else {
      // escritorio 
      localStorage.setItem('clave', this.clave);
    }
  }

  cargarStorage() {
    console.log('usuario.ts función cargarStorage');
    return new Promise( (resolve, reject) => {

          if (this.platform.is('cordova'))  {
            // dispositivo móvil
            this.storage.get('clave').then((val) => {
                   if (val) {
                     this.clave = val;
                     console.log('(Nativo) Valor hallado en usuario.ts función cargarStorage ', this.clave);
                     resolve(true);
                   } else {
                    console.log('(Nativo) Valor de clave no hallado en usuario.ts función cargarStorage ');
                     resolve(false);
                   }
              });
              
         } else {
           // escritorio 
           if ( localStorage.getItem('clave') ){
             this.clave = localStorage.getItem('clave');
             console.log('Valor hallado en usuario.ts función cargarStorage ', this.clave);
             resolve(true);
           } else {
            console.log('Valor de clave no hallado en usuario.ts función cargarStorage ');
             resolve(false);
           }
         }
    });
  }

  borrarStorage() {
    console.log('En usuario.ts función borrarStorage');
    this.clave = null;

    if (this.platform.is('cordova'))  {
        // dispositivo móvil
          this.storage.remove('clave');
    } else {
       // escritorio 
         localStorage.removeItem('clave');
    }

    this.doc.unsubscribe();

  }
}



