import { UbicacionProvider } from './../../providers/ubicacion/ubicacion';
import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { LoginPage } from '../login/login';


@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage { 
  usuario: any = {} ;

  constructor(
              public navCtrl: NavController, 
              public _ubicacionProv: UbicacionProvider) {

      console.log('home.ts - constructor() ');      

       this._ubicacionProv.inicializarChofer();
       this._ubicacionProv.iniciarGeolocalizacion();
       this._ubicacionProv.chofer.valueChanges()
             .subscribe( data => {
                  this.usuario = data;
                  console.log('ubicacion.ts - constructor suscrito con ' , 
                                JSON.stringify( this.usuario));
             });            

  }

  salir() {
    console.log('home.ts - salir() cerrando sesión para ' ,
               this._ubicacionProv._usuarioProvider.clave);
    this._ubicacionProv.detenerUbicacion();
    this._ubicacionProv._usuarioProvider.borrarStorage();
    this.navCtrl.setRoot(LoginPage);    
  }

}



